#home-loader-wrap {

    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999;
    visibility: visible;
    transition: all 2s ease;
    top: 0;
    left: 0;
}

#home-loader {
    position: fixed;
    z-index: 999;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    margin: -40px 0 0 -40px;
    font-size: 10px;
    text-indent: -9999em;
    border-radius: 50%;
    background: $color;
    background: -moz-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: -webkit-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: -o-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: -ms-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    -webkit-animation: load3 1.4s infinite linear;
    animation: load3 1.4s infinite linear;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
}

#home-loader:before {
    width: 50%;
    height: 50%;
    background: $color;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
}

#home-loader:after {
    background: #fff;
    width: 76px;
    height: 76px;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

@keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.index {
    .link a {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }

    .idx_box {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        &::before, &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            display: block;
        }

        &::before {
            left: -10px;
            bottom: -10px;
            z-index: -1;
        }

        &::after {
            left: 0px;
            bottom: 0px;
            z-index: -1;
            border: 1px solid $color;
        }

        &.pic1 {
            &::before {
                width: 90px;
                height: 90px;
                right: -50px;
                bottom: -40px;
                left: auto;
                background-color: #fff;
            }

            &::after {
                left: 10px;
                bottom: 10px;
                z-index: 1;
            }
        }

        &.pic2 {
            &::before {
                width: 100px;
                height: 100px;
                top: -50px;
                left: -50px;
                bottom: auto;
                background-color: #EDF7F8;
            }

            &::after {
                left: 10px;
                bottom: 10px;
                z-index: 1;
            }
        }
    }

    .main_visual {
        position: relative;
        height: 730px;
        max-width: 1920px;
        margin: 28px auto 0;

        &::before {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            right: -50px;
            width: 500px;
            max-width: 27%;
            height: 500px;
            background-color: #EDF7F8;
        }

        .key {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding-left: 22px;
            padding-bottom: 20px;
        }

        .idx_box {
            padding-top: 10px;
            padding-right: 5px;
            width: 740px;
            height: 360px;
            transform: scale(0.5);
            transition: all .6s;
            &::before {
                background-color: rgba($color: #fff, $alpha: 0.95);
            }

            .sub {
                margin-bottom: 3.3em;
                text-align: center;
                font-size: 13px;
                font-family: $fnotosans;
                letter-spacing: 0.08em;
            }

            .flex_col3 {
                display: flex;
                justify-content: center;

                p {
                    margin-bottom: 0;
                    height: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 0px 33px;
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 1.3333;
                    letter-spacing: 0.0em;
                    text-align: center;
                    font-family: $fnotoserif;
                    border-left: 1px solid $color2;

                    &:first-child {
                        border-left: 0;
                    }
                }
            }
        }

        h2 {
            margin-bottom: 0.09em;
            text-align: center;
            font-size: 40px;
            font-family: $fnotoserif;
            line-height: 58px;
            font-weight: 400;
            letter-spacing: 0.22em;

            .blue {
                color: #50B0B8;
            }
        }
    }

    picture {
        display: block;
    }

    h3 {
        margin-bottom: 25px;
        font-size: 36px;
        font-family: $fnotoserif;
        line-height: 1.5;
        letter-spacing: 0.03em;

        .en {
            margin-bottom: 8px;
            font-family: $fen;
            display: block;
            font-size: 30px;
            letter-spacing: 0.05em;
        }

        &.st2 {
            text-align: center;

            &::after {
                content: "";
                margin: 34px auto 0;
                display: block;
                width: 50px;
                height: 1px;
                background-color: $color2;
            }

            .sub {
                padding-bottom: 10px;
                margin-top: 7px;
                display: block;
                font-size: 13px;
                letter-spacing: 0.05em;
                font-family: $fnotosans;
            }
        }
    }

    .blue {
        color: #50B0B8;
    }

    h4 {
        position: relative;
        display: table;
        margin: auto;
        margin-bottom: 0.09em;
        text-align: center;
        font-size: 30px;
        font-family: $fnotoserif;

        &.white {
            color: #fff;

            &::after, &::before {
                display: none;
            }
        }

        .cl {
            color: #C29C61;
        }

        &::after, &::before {
            content: "";
            margin: 0;
            position: absolute;
            top: calc(50% + 2px);
            display: block;
            width: 100vw;
            height: 1px;
            background-color: #333333;
        }

        &::before {
            left: 102%;
        }

        &::after {
            right: 102%;
        }
    }

    .txt {
        font-size: 15px;
        line-height: 32px;
        letter-spacing: 0.05em;
        font-family: $fnotosans;
    }

    .list {
        margin-top: 11px;
        margin-bottom: 0;
        position: relative;
        padding-left: 19px;
        font-family: $fnotoserif;
        font-size: 20px;
        font-weight: 500;

        &::before {
            content: "";
            background-color: #C29C61;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            left: 1px;
            top: 15px;
            position: absolute;
            display: block;
        }
    }
}
