            picture {
                transition-delay: 1s;
                opacity: 1;
            }
        }

        &::before {
            display: none;
            content: "";
            display: block;
            background: #fff;
            z-index: 1;
            width: 130%;
            height: 120%;
            left: -10%;
            top: 0;
            position: absolute;
            -webkit-transform: skewX(15deg);
            transform: skewX(15deg);
            -webkit-animation: fadein1 2s 1 ease forwards;
            animation: fadein1 2s 1 ease forwards;
        }
    }

    &.aos-animate:after {
        content: "";
        display: block;
        background: $color2;
        z-index: 2;
        width: 0%;
        height: 120%;
        left: -10%;
        top: 0;
        position: absolute;
        -webkit-transform: skewX(15deg);
        transform: skewX(15deg);
        -webkit-animation: fadein0 3s 1 ease forwards;
        animation: fadein0 3s 1 ease forwards;
    }
}

@keyframes fadein0 {
    0% {
        width: 0%;
        left: -12.5%;
        opacity: 1;
    }

    30% {
        width: 130%;
        left: -12.5%;
        opacity: 1;
    }

    60% {
        width: 130%;
        left: 130%;
        opacity: 1;
    }

    100% {
        width: 130%;
        left: 130%;
        opacity: 0;
    }
}

@keyframes fadein1 {
