.under {
    .main_visual {
        margin-top: 28px;
        padding: 0px 10px;

        .key {
            margin: auto;
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: 1800px;
            background-image: url(../images/ud_mv.jpg);
            background-position: center;
            background-size: cover;
            height: 300px;
        }

        h2 {
            font-size: 40px;
            font-weight: 400;
            letter-spacing: 0.25em;
            font-family: $fnotoserif;
            color: #fff;
        }
    }

    .topic_path {
        padding: 18px 0 16px;

        ul {
            display: flex;
            align-items: center;
            padding: 0;

            .arr {
                display: inline-block;
                position: relative;
                width: 24px;

                &::before {
                    content: "";
                    position: absolute;
                    right: 0;
                    bottom: -1px;
                    height: 0px;
                    width: 0px;
                    border-left: 8px solid #C29C61;
                    border-bottom: 6px solid transparent;
                    border-top: 6px solid transparent;
                    transition: all .3s;
                }
            }

            li {
                position: relative;
                font-size: 15px;
                line-height: 1.45;
                letter-spacing: 0.1em;
                color: #000000;
                margin-right: 20px;

                a {
                    text-decoration: none;

                    &:hover {
                        color: $color;
                    }
                }
            }
        }
    }

    .content {
        font-family: $fnotosans;
        p, dt, dl {
      
            font-size: 15px;
            letter-spacing: 0.00em;
            line-height: 32px;
        }
    }

    .box_h3 {
        margin: 0;
    }

    .blue {
        color: #50B0B8;
    }

    .bg_blue {
        padding: 72px 0px 70px;
        position: relative;
        z-index: 1;

        &::before {
            content: "";
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 100vw;
            height: 100%;
            top: 0;
            z-index: -1;
            background-color: #EDF7F8;
        }
    }

    h3 {
        margin-bottom: 29px;
        text-align: center;
        font-size: 36px;
        font-family: $fnotoserif;
        line-height: 1.5;

        .en {
            margin-bottom: 8px;
            font-family: $fen;
            display: block;
            font-size: 30px;
            letter-spacing: 0.05em;
        }
    }

    .box_h4 {
        margin-top: 90px;
    }

    h4 {
        margin-bottom: 32px;
        text-align: center;
        font-size: 36px;
        font-family: $fnotoserif;
        line-height: 1.5;

        &::after {
            content: "";
            margin: 18px auto 0;
            display: block;
            width: 50px;
            height: 1px;
            background-color: $color2;
        }
    }

    .box_h5 {
        margin-top: 70px;
        overflow: hidden;
    }

    h5 {
        margin-bottom: 22px;
        position: relative;
        display: table;
        font-size: 30px;
        font-family: $fnotoserif;
        line-height: 1.5;

        &::after {
            content: "";
            margin: 0;
            position: absolute;
            left: 103.5%;
            top: calc(50% + 1px);
            display: block;
            width: 100vw;
            height: 1px;
            background-color: #333333;
        }
    }

    h6 {
        margin-bottom: 15px;
        position: relative;
        display: table;
        font-size: 24px;
        font-family: $fnotoserif;
        line-height: 1.5;
        color: #50B0B8;
    }

    .gr_btn {
        margin-top: 60px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        .btn {
            margin: 10px 25px;

            a {
                letter-spacing: 0;
            }
        }
    }

    .box_cont {}

    .image_r,
    .image_l {
        margin-top: 10px;
        position: relative;
        z-index: 1;
        max-width: 450px;
    }

    .image_r {
        float: right;
        margin-left: 50px;
    }

    .image_l {
        float: left;
        margin-right: 50px;
    }

    p:last-child {
        margin-bottom: 0;
    }

    .clearfix:after {
        content: ".";
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
    }

    .ud_col2, .ud_col3 {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .ud_col2 {
        .box {
            width: 460px;
            margin-bottom: 20px;
        }

        .tt {
            margin-bottom: 20px;
            position: relative;
            text-align: center;
            margin-left: auto;
            margin-right: auto;
            padding: 0px 40px;
            display: table;
            font-size: 30px;
            font-family: $fnotoserif;
            line-height: 1.5;
            letter-spacing: -0.05em;

            &::after, &::before {
                content: "";
                margin: 0;
                position: absolute;
                width: 30px;
                top: calc(50% + 2px);
                display: block;
                left: auto;
                height: 1px;
                background-color: #50afb8;
            }

            &::after {
                right: 0;
            }

            &::before {
                left: 0;
                right: auto;
            }
        }
    }

    .ud_col3 {
        .box {
            width: 300px;
        }

        &.step {
            margin-bottom: 125px;
        }
    }

    .ud_step {
        .tt_step {
            margin-bottom: 18px;
            font-family: $fen;
            font-size: 26px;
            text-align: center;

            .num {
                display: inline-block;
                font-size: 200%;
            }
        }

        .step {
            position: relative;

            &:not(:last-child)::after {
                content: "";
                position: absolute;
                left: calc(50% - 16px);
                bottom: -82px;
                height: 0px;
                width: 0px;
                border-top: 22px solid #C29C61;
                border-left: 15px solid transparent;
                border-right: 15px solid transparent;
                transition: all .3s;
            }
        }

        &.bg_blue {
            margin-top: 70px;

            .tt {
                margin-bottom: 0px;
                position: relative;
                display: table;
                font-size: 30px;
                font-family: $fnotoserif;
                line-height: 1.5;
                letter-spacing: 0;
            }
            .tt_step{
                margin-right: 17px;
            }

            .step {
                &:not(:last-child){
                    margin-bottom: 67px;
                    &::after {
                        bottom: -51px;
                    }
                }
            }
        }
    }
    ul.list01 {
        li {
            position: relative;
            margin-bottom: 6px;
            padding-left: 15px;
            font-weight: 300;
            font-size: 15px;
            letter-spacing: 0.08em;
            font-family: $fnotosans;

            &:last-child {
                margin-bottom: 0;
            }

            &::before {
                content: "";
                background-color: $color;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                left: 0;
                top: 10px;
                position: absolute;
                display: block;
            }
        }
    }
}

@media screen and (min-width: 751px) {
    .under {
        .boxImg_item.small {

            .image_r,
            .image_l {
                max-width: 350px;
            }
        }

        .boxImg_item.small2 {

            .image_r,
            .image_l {
                max-width: 300px;
            }

            .image_r {
                margin-left: 70px;
            }

            .image_l {
                margin-right: 70px;
            }
        }
    }
}