#header {
    margin: auto;
    width: 100%;
    padding: 0px 0px 0px 54px;
    min-width: 1260px;

    .inner {
        display: flex;
        justify-content: space-between;
        max-width: 100%;
    }

    .h1_txt {
        display: block;
        margin-top: 12px;
        margin-bottom: 41px;
        font-size: 12px;
        letter-spacing: 0.05em;
        //   font-family: Meiryo,arial,clean,verdana,'Hiragino Kaku Gothic Pro',"ＭＳ Ｐゴシック",Osaka,sans-serif;
    }

    .h_add p {
        margin-bottom: 0;
        margin-right: 25px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: $fnotosans;
        font-weight: 300;
        font-size: 15px;
        letter-spacing: 0.05em;
        line-height: 1.4;
    }

    .h_right {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-left: 10px;
        height: 100px;
        box-shadow: 0px 3px 8px 1px rgba($color: #000000, $alpha: 0.05);

        .kidsortho {
            a {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 318px;
                height: 100px;
                background-color: $color;
                font-size: 16px;
                color: #fff;
                font-weight: 400;
                font-family: $fnotoserif;
                letter-spacing: 0em;
                text-decoration: none;
                text-align: center;
                line-height: 23px;

                &:hover {
                    background-color: $color2;
                }

                &:before {
                    content: "";
                    position: absolute;
                    right: 7px;
                    bottom: 7px;
                    height: 0px;
                    width: 0px;
                    border-bottom: 8px solid #fff;
                    border-left: 8px solid transparent;
                }
            }
        }
    }
}

#top {
    width: 30%;

    img {
        image-rendering: -webkit-optimize-contrast;
    }
}
.menu_toggle .menu_list > ul > li     >ul {

    background-color: #fff;

    transition: all .3s;

    a {
        display: block;
        position: relative;
        padding: 12px 11px 12px 36px;
        font-size: 14px;
        color: #fff;
        font-weight: 400;
        font-family: $fnotosans;
        text-decoration: none;
        color: #333333;
        letter-spacing: 0.08em;
        border-bottom: 1px solid #EFEFEF;

        &::before {
            content: "";
            display: block;
            position: absolute;
            left: 10px;
            bottom: 23px;
            height: 1px;
            width: 10px;
            background-color: #333333;
        }
    }
}
@media only screen and (min-width:751px) {
    .menu_toggle {
        position: relative;
        z-index: 2;
        padding: 0px 22px 8px 10px;

        .menu_list>ul {
            display: flex;
            justify-content: center;

            >li {
                position: relative;

                &:hover {
                    >ul {
                        opacity: 1;
                        z-index: 1;
                        visibility: visible;
                    }
                }

                &:nth-child(2), &:nth-child(3) {
                    a {
                        letter-spacing: 0.04em;
                    }
                }

                &:hover {
                    >a {
                        color: $color2;
                    }

                    &.menu_sub {
                        >a::before {
                            bottom: 0px;
                        }
                    }
                }

                &.menu_sub {
                  ul  a{
                        &:hover {
                            color: $color;
        
                            &::before {
                                background-color: $color;
                            }
                        }   
                    }
                    >a::before {
                        content: "";
                        position: absolute;
                        left: calc(50% - 6px);
                        bottom: 9px;
                        height: 0px;
                        width: 0px;
                        border-top: 8px solid #C29C61;
                        border-left: 6px solid transparent;
                        border-right: 6px solid transparent;
                        transition: all .3s;
                    }
                }

                >ul {
                    position: absolute;
                    top: 73px;
                    width: 294px;
                    left: -8px;
                    opacity: 0;
                    visibility: hidden;
                    border: 1px solid #E0CDB0;
                }

                >a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 20px 21px;
                    font-size: 16px;
                    color: #fff;
                    font-weight: 400;
                    font-family: $fnotoserif;
                    text-decoration: none;
                    color: #333333;
                    letter-spacing: 0.08em;
                }
            }
        }
    }
}

.bottom_bar {
    position: fixed;
    right: 0;
    top: 200px;
    width: 100px;
    z-index: 10;

    p {
        margin: 0;
    }

    >div a {
        position: relative;
        width: 100px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-decoration: none;
        color: #fff;
        font-size: 16px;
        font-family: $fnotoserif;

        &:hover {
            .ov {
                right: 0;
            }
        }

        .ov {
            position: absolute;
            z-index: 1;
            right: -282px;
            width: 282px;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 27px;
            letter-spacing: 0.05em;
            transition: all .3s;

            &::before {
                content: "";
                display: block;
                margin-right: 20px;
                width: 37px;
                height: 32px;
                background-position: center;
                background-size: 100% auto;
                background-repeat: no-repeat;
                z-index: 1;
            }

            &::after {
                content: "";
                position: absolute;
                z-index: -1;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                opacity: 0.3;
            }
        }

        &::before {
            content: "";
            display: block;
            width: 50px;
            height: 50px;
            background-position: center;
            background-repeat: no-repeat;
        }
    }

    .box1 a {
        background-color: #C29C61;

        &::before {
            background-image: url(../images/ft_ic1.png);
            background-size: 41px auto;
            height: 39px;
        }
    }

    .box2 a {
        background-color: #333333;

        .ov {
            background-color: #272727;

            &::before {
                background-image: url(../images/ft_ic2ov.png);
            }
        }

        &::before {
            background-image: url(../images/ft_ic2.png);
            background-size: 25px auto;
            height: 32px;
        }
    }

    .box3 a {
        background-color: #51B7A4;

        .ov {
            background-color: #40A792;

            &::before {
                background-image: url(../images/ft_ic3ov.png);
            }
        }

        &::before {
            background-image: url(../images/ft_ic3.png);
            background-size: 47px auto;
            height: 24px;
        }
    }

    .box4 a {
        background-color: #519CB7;

        .ov {
            background-color: #408AA7;

            &::before {
                background-image: url(../images/ft_ic4ov.png);
            }
        }

        &::before {
            background-image: url(../images/ft_ic4.png);
            background-size: 36px auto;
            height: 36px;
        }
    }
}

#footer {
