    }
}

.btn {
    position: relative;
    width: 300px;
    height: 60px;
    color: #333;
    border: 1px solid;
    transition: all .3s;

    &.white {
        color: #fff;

        a {
            background-image: url(../images/ic_btnov.png);

            &:before, &::after {
                background: $color2;
            }
        }

        &:hover {
            a {
                background-color: $color2;
            }
        }

        &:before, &::after {
            background: $color2;
        }
    }

    &:before, &::after {
        content: "";
        position: absolute;
        width: 0;
        height: 1px;
        background: $color;
        transition: all 0.2s linear;
    }

    &::before {
        left: 0;
        top: 0;
        transition-duration: 0.4s;
    }

    &::after {
        right: 0;
        bottom: 0;
        transition-duration: 0.4s;
    }

    &:not(.link_map):hover {
        outline-width: 0;
        border-color: transparent;

        &:before, &::after {
            width: 100%;
        }

        a {
            background-color: $color;
            color: #fff;
            background-image: url(../images/ic_btnov.png);
            transition-delay: 0.5s;
            color: #fff;

            &:before, &::after {
                height: 100%;
            }
        }
    }

    a {
        width: 100%;
        height: 100%;
        outline-width: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        color: inherit;
        font-size: 15px;
        letter-spacing: 0.08em;
        text-decoration: none;
        font-family: $fnotoserif;
        transition: all .3s;
        background-image: url(../images/iic_btn.png);
        background-repeat: no-repeat;
        background-size: 8px;
        background-position: bottom 9px right 9px;

        &:before, &::after {
            content: "";
            position: absolute;
            width: 1px;
            height: 0;
            background: $color;
            transition: all 0.2s linear;
        }

        &::before {
            left: 0;
            top: 0;
            transition-duration: 0.4s;
        }

        &::after {
            right: 0;
            bottom: 0;
            transition-duration: 0.4s;
        }
    }
}

@media screen and (max-width:1700px) and (min-width:751px) {
    #header {
        padding-left: 10px;

        .h_right {
            max-width: 69%;

            .kidsortho a {
                max-width: 15vw;
                padding: 10px 20px;
            }
        }
    }

    .menu_toggle .menu_list>ul>li>a {
        padding: 20px 0.7vw;
    }
}

@media screen and (max-width:1560px) and (min-width:751px) {
    #top {
        width: 39%;
    }
    .menu_toggle .menu_list > ul > {
        li:nth-child(6),li:nth-child(5){
            >ul{
                left: -144px;
            }
        }
    }
    #header .h_right {
        width: 750px;
        max-width: 58%;
        height: 180px;
        align-items: flex-start;
        flex-wrap: wrap;

        .h_add p {
            height: 80px;
        }

        .kidsortho a {
            height: 80px;
            max-width: 318vw;
            padding: 10px 20px;
        }

        .menu_toggle {
            order: 3;
            width: 100%;
        }
    }

    .menu_toggle .menu_list>ul>li>a {
        padding: 20px 18px;
    }
}