@charset "UTF-8";
@import "../css/base.css";
@import "../css/fonts.css";
@import "../css/normalize.css";
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond&family=Noto+Sans+JP:wght@300;400;500&family=Noto+Serif+JP:wght@400;500;600&display=swap');
$color : #50B0B8;
$color2 : #C29C61;
$fnotoserif : 'Noto Serif JP', serif;
$fnotosans: 'Noto Sans JP', sans-serif;
$fen: 'Cormorant Garamond', serif;
h1, h2, h3, h4, h5, h6, input, button, textarea, select, p, blockquote, th, td, pre, address, li, dt, dd {
  font-size: 140%;
  -webkit-text-size-adjust: none;
}
header, section, footer, aside, nav, main, article, figure {
  display: block;
}
body{
color: #333333;
}
img {
  vertical-align: bottom;
}
a {
  color: #333333;
}
a:hover, a:active {
  text-decoration: none;
}
table {
  width: 100%
}
p {
  margin: 0 0 1.5em;
}
section, .section {
  margin: 0 0 60px;
}
p, dd, td, th, li {
  line-height: 1.8em;
}
.image-l {
  float: left;
  margin: 0 40px 8px 0;
}
.image-r {
  float: right;
  margin: 0 0 8px 40px;
}
#totop {
  position: fixed;
  bottom: 60px;
  right: 50px;
  z-index: 10;
  margin-bottom: 0;
}
.bold {
  font-weight: bold;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
*,*:before, *:after {
  box-sizing: border-box;
}
/*==========================================================================*/
/*                            Container                                     */
/*==========================================================================*/
#wrapper {
  min-width: 1260px;
  overflow: hidden;
}
.container {
  width: 1020px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  box-sizing: border-box;
}
img {
  max-width: 100%;
}

